export default {
	data() {
		return {
			clientHeight: '',
			customTheme: '',
			location: '',
			swiperHeight: 'height:2800upx;',
			tabCurrentIndex: 0,
			swiperCurrentIndex: 0,
			tabs: [
				// {
				// name: '猎',
				// id: 'guowai'
				// },
				// {
				// name: '小贴士',
				// id: 'qita'
				// }
			],
			titleShowId: 'tabTag-0',

			//info
			info: {
				location_head_img_url: '',
				items: [],
			},
		}
	},
	onLoad(e) {
		this.customTheme = this.$store.state.customTheme;
		uni.showLoading({
			title: '数据加载中',
			mask: true
		});
		this.getInfo(e)
	},
	onReady() {

	},
	methods: {
		getInfo(e) {
			let _this = this
			this.$http({
				url: '/location/' + e.id
			}).then(res => {
				this.location = res
				uni.getSystemInfo({
					success: res1 => {
						_this.clientHeight = res1.windowHeight
					}
				})
			})
		},
		//项目列表滑动到底部
		scrolltoBottom(e) {
			// if (temp) {
			// 	temp = 0
			// 	let a = this.institutionList
			// 	let lastId = a[a.length - 1].institution_id
			// 	console.log(lastId)
			// 	this.getInstitutionInfo(lastId)
			// 	setTimeout(() => {
			// 		temp = 1
			// 	}, 1000);
			// }
		},
		itemJump(index) {
			console.log(this.location)
			console.log(index);
			uni.navigateTo({
				url: `../home/item?id=${this.location.items[index].item_id}`,
				animationType: 'pop-in',
				animationDuration: 200
			});
		},
		tabChange: function(e) {
			var index = e.target.id.replace('tabTag-', '')*1;
			this.swiperCurrentIndex = index;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		swiperChange: function(e) {
			var index = e.detail.current;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
			console.log(index);
		}
	}
}
